.os-container {
  display: block;
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
}

.os-overflow-text-hidden {
  min-width: 0;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

